@import url(https://fonts.googleapis.com/css2?family=DotGothic16&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app__btn,.app__btn--dark-mode{margin-left:15px;border-radius:5px;text-align:center;background:none;padding:2px 10px;font-family:'DotGothic16', sans-serif}.app{text-align:center}.app__btn{border:solid 1px #05384b;color:#05384b}.app__btn--dark-mode{border:solid 1px cyan;color:cyan}.app--dark-mode{text-align:center;background:#05384b}.app-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.app-logo{animation:app-logo-spin infinite 20s linear}}.app-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.app-link{color:#61dafb}@keyframes app-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

.search{-webkit-appearance:none;border:1px solid #fcf7f7;outline:none;border-radius:35px;padding:5px 20px;width:200px;line-height:30px;margin-bottom:25px;background:whitesmoke;font-feature-settings:"smcp", "c2sc";font-variant-caps:all-small-caps}.search:focus{border-radius:35px;border:2px solid #0ff;background:none}

.header{display:flex;align-items:center;justify-content:center;height:65px}.header__text{font-family:"Courier New", Courier, monospace;color:#00e3e3;font-size:60px;font-weight:600;font-feature-settings:"smcp";font-variant:small-caps}.header__img{width:50px}

.card-list{width:85vw;margin:0 auto;display:grid;grid-template-columns:1fr;grid-gap:20px}@media (min-width: 767px){.card-list{grid-template-columns:1fr 1fr 1fr 1fr}}

.card,.card--dark-mode{display:flex;flex-direction:column;border:1.8px solid #00e3e3;border-radius:5px;padding:25px;cursor:pointer;-moz-osx-font-smoothing:grayscale;-webkit-backface-visibility:hidden;backface-visibility:hidden;transform:translateZ(0);transition:transform 0.25s ease-out;box-shadow:5px 5px 4px cyan}.card{background:aliceblue}.card--dark-mode{background:none}.card__header{display:flex;justify-content:space-evenly;align-items:baseline}.card__img{width:120px;height:125px;margin:0 auto}.card__title{font-family:"Courier New", Courier, monospace;font-size:18px}.card__title--dark-mode{font-family:"Courier New", Courier, monospace;font-size:18px;color:whitesmoke}.card__id{font-size:16px;font-family:monospace;font-weight:700;color:#0cc7c7}.card:hover{transform:scale(1.05)}

.load-more__btn,.load-less__btn{font-size:16px;padding:6px 15px;border-radius:35px;background-color:#ff932c;border:none;cursor:pointer;margin:20px 8px;color:white;font-feature-settings:"smcp";font-variant:small-caps}.load-more__btn:hover,.load-less__btn:hover{background-color:#feb23e}

